$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Countdown Time
.wp-block-ttb-text-typing {
	* {
		box-sizing: border-box;
	}

	.ttbTextTyping {
		.typed-fade-out {
			opacity: 0;
			transition: opacity .25s;
			animation: 0;
		}

		.typed-cursor {
			opacity: 1;

			&.typed-cursor--blink {
				animation: typingCursorBlink 0.7s infinite;
			}
		}

		@keyframes typingCursorBlink {
			0% {
				opacity: 1;
			}

			50% {
				opacity: 0.0;
			}

			100% {
				opacity: 1;
			}
		}
	}
}